import React, { Component } from 'react';
import { connect } from "react-redux";
import { Popover, Icon, message, Button } from 'antd';
import TotalBreadcrumb from './Breadcrumb';
import './topcomponent.css';
import logo from '../../../assets/img/logoTitle.png';
import { clearLocal,  getLocal } from '../../../utils';
import { loginAction } from '../../../redux/actions/loginAction'
import { NavLink, withRouter } from 'react-router-dom';
import ChangePassModal from "./ChangePassModal";
import {postAction} from "../../../axios";

class TopComponent extends Component{
    state = {
        changePassShow: false,
    }
    changePass = () => {
        this.setState({
            changePassShow: true,
        });
    }
    handleOk = (values) => {
        const userId = JSON.parse(localStorage.getItem('userInfo')).id;
        const data = { sourceCode:'pc', userId:userId, originalPassword: values.oldPass, currentPassword: values.newPass, isEncrypt: false }
        postAction("/bigdata/system/user/update/password", data, 0,1).then(
            (res) => {
                if (res.success) {
                    message.success('密码修改成功！');
                    this.setState({
                        changePassShow: false,
                    });
                } else {
                    message.warn(res.obj);
                }
            }
        )
    }
    handleCancel = () => {
        this.setState({
            changePassShow: false,
        });
    }

    jumpData = () => {
        this.props.history.push("/bigscreen/skill")
    }

    render () {
        const userName = JSON.parse(getLocal("userInfo")).name;
        const content = (<div>
            {/*单点登录第四处修改  注释掉修改密码*/}
            {/* <p><a onClick={this.changePass}><Icon type="form" theme="outlined" style={{ marginRight: 5 }} />修改密码</a></p> */}
            <p style={{ marginBottom: 0 }}><a onClick={this.props.logout}><Icon type="poweroff" theme="outlined" style={{ marginRight: 5 }} />退出登录</a></p>
        </div>);
        return (
            <div className='topcomponent'>
                <NavLink to='/' className='logo'>                    
                	<img src={logo} alt="" style={{width:90, float:"left", marginTop:15, marginLeft:0}} />
                    {/* <div style={{height: 60, width:138,float: "left",lineHeight: "20px", color: "white", marginTop:"20px"}}>
                        <span>AIod学生安全与成长<br />辅助决策平台</span>
                    </div> */}
                    <span style={{color:"#fff", fontSize:14, fontWeight:450, float:"left", marginLeft:20}}>三盟科技大学</span>
                </NavLink>
                <TotalBreadcrumb />
                <Popover content={content}>
                    <span className="username">
                        欢迎，{userName}
                        <Icon type="setting" theme="outlined" style={{marginLeft: '20px', fontSize:'18px'}} />
                    </span>
                </Popover>
                <Button 
                    type="primary" 
                    style={{float:"right", marginRight: 20, marginTop: 24}}
                    onClick={this.jumpData}
                >
                    返回领导驾驶舱
                </Button>
                {
                    this.state.changePassShow ?(
                        <ChangePassModal
                            handleOk={this.handleOk}
                            handleCancel={this.handleCancel}
                        />
                    ):null
                }
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        logout () {
            clearLocal();
            // 单点登录地址   //单点登录第五处修改
            // window.location.href = 'http://10.64.192.20:6050/bigdata/user/singleLogout';
            const action = loginAction(false);
            dispatch(action);
        }
    }
}

export default connect(null, mapDispatchToProps)(withRouter(TopComponent))


