import React from 'react';
import { connect } from 'react-redux';
import { setLocal, getLocal } from './../../../utils';
import { Breadcrumb } from 'antd';

class TotalBreadcrumb extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			first: "",
			second: ""
		};
		this.changeState = this.changeState.bind(this);
	}

	componentDidMount() {
		this.changeState()
	}

	componentWillReceiveProps(nextProps) {
		if (JSON.stringify(this.props) !== JSON.stringify(nextProps)) {
			setLocal("breadcrumb", nextProps.totalBreadcrumb);
			this.changeState();
		}
	}

	changeState() {
		let curUrl = window.location.href.split("#")[1];
		let breadcrumb = getLocal("breadcrumb");

		if (curUrl === breadcrumb) {
			if (breadcrumb === "/mainframe/xingweihuaxiang/all") {
				this.setState({
					first: "行为画像",
					second: "群体画像"
				})
			} else if (breadcrumb === "/mainframe/xingweihuaxiang/personal") {
				this.setState({
					first: "行为画像",
					second: "个人画像"
				})
			} else if (breadcrumb === "/mainframe/xingweihuaxiang/personal/details") {
				this.setState({
					first: "行为画像",
					second: "个人画像"
				})
			} else if (/\/mainframe\/comprehensivewarning/.test(breadcrumb)) {
				this.setState({
					first: "综合预警",
					second: ""
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/pinkunshengfenxi") {
				this.setState({
					first: "精准资助",
					second: "家庭经济困难学生分析"
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/pinkunshengchaxun") {
				this.setState({
					first: "精准资助",
					second: "家庭经济困难学生查询"
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/detail") {
				this.setState({
					first: "精准资助",
					second: "家庭经济困难学生查询"
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/yichangpinkunsheng") {
				this.setState({
					first: "精准资助",
					second: "异常家庭经济困难学生"
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/detail2") {
				this.setState({
					first: "精准资助",
					second: "异常家庭经济困难学生"
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/jianyiguanaipinkunsheng") {
				this.setState({
					first: "精准资助",
					second: "建议关爱学生"
				})
			} else if (breadcrumb === "/mainframe/jinzhunzizhu/detail3") {
				this.setState({
					first: "精准资助",
					second: "建议关爱学生"
				})
			} else if (breadcrumb === "/mainframe/mentalhealth/analysis") {
				this.setState({
					first: "心理健康",
					second: "心理健康分析"
				})
			} else if (breadcrumb === "/mainframe/mentalhealth/warn") {
				this.setState({
					first: "心理健康",
					second: "心理健康预警"
				})
			} else if (/\/mainframe\/mentalhealth\/management/.test(breadcrumb)) {
				this.setState({
					first: "心理健康",
					second: "心理访谈管理"
				})
			} else if (/\/mainframe\/systemsetup\/earlywarningsetting/.test(breadcrumb)) {
				this.setState({
					first: "系统设置",
					second: "预警设置"
				})				
			} else if (/\/mainframe\/systemsetup\/dabiao/.test(breadcrumb)) {
				this.setState({
					first: "系统设置",
					second: "资助达标金额配置"
				})
			} else if (breadcrumb === "/mainframe/systemsetup/dabiaoDetail") {
				this.setState({
					first: "系统设置",
					second: "资助达标金额配置"
				})
			} else if (/\/mainframe\/systemsetup\/earlywarningconfig/.test(breadcrumb)) {
				this.setState({
					first: "系统设置",
					second: "预警设置"
				})
			} else if (/\/mainframe\/systemsetup\/earlywarningwhitelist/.test(breadcrumb)) {
				this.setState({
					first: "系统设置",
					second: "预警白名单设置"
				})
			} else if (breadcrumb === "/mainframe/accessmanage/account") {
				this.setState({
					first: "权限管理",
					second: "账户管理"
				})
			} else if (breadcrumb === "/mainframe/accessmanage/menu") {
				this.setState({
					first: "权限管理",
					second: "菜单管理"
				})
			} else if (breadcrumb === "/mainframe/accessmanage/authority") {
				this.setState({
					first: "权限管理",
					second: "权限配置"
				})
			} else {
				this.setState({
					first: "行为画像",
					second: "群体画像"
				})
			}
		} else {
			setLocal("breadcrumb", curUrl);
			this.changeState();
		}
	}

	render() {
		return (
			<React.Fragment>
				<Breadcrumb className="breadcrumb">
					<Breadcrumb.Item>{this.state.first}</Breadcrumb.Item>
					<Breadcrumb.Item>{this.state.second}</Breadcrumb.Item>
				</Breadcrumb>
			</React.Fragment>
		)
	}
}

const mapStateToProps = (state) => {
	return {
		totalBreadcrumb: state.breadcrumbReducer.totalBreadcrumb
	}
}

export default connect(mapStateToProps, null)(TotalBreadcrumb)


