import React from 'react';
import {Switch, Route, withRouter, Redirect} from 'react-router-dom';
import { asyncComponent } from '../../../utils';
import './rightcomponent.css'

// 获取到异步组件

// 行为画像
// 群体画像
const All = asyncComponent(() => import('../../../pages/xingweihuaxiang/all'));
// 个人画像列表页
const Personal = asyncComponent(() => import('../../../pages/xingweihuaxiang/personal'));
// 个人画像详情页
const PersonalDetails = asyncComponent(() => import('../../../pages/xingweihuaxiang/personal/details'));


//综合预警
//列表页
const ComprehensiveWarning = asyncComponent(() => import('../../../pages/ComprehensiveWarning')); 
//详情页
const ComprehensiveWarningDetail = asyncComponent(() => import('../../../pages/ComprehensiveWarning/Detail/Detail')); 
// 预警概况
const ComprehensiveWarningOverview = asyncComponent(() => import('../../../pages/ComprehensiveWarning/Overview')); 

// 精准资助
// 家庭经济困难学生分析
const Pinkunshengfenxi = asyncComponent(() => import('../../../pages/jinzhunzizhu/pinkunshengfenxi')); 
// 家庭经济困难学生查询
const Pinkunshengchaxun = asyncComponent(() => import('../../../pages/jinzhunzizhu/pinkunshengchaxun')); 
// 异常家庭经济困难学生
const Yichangpinkunsheng = asyncComponent(() => import('../../../pages/jinzhunzizhu/yichangpinkunsheng')); 
// 建议关爱学生
const Jianyiguanaipinkunsheng = asyncComponent(() => import('../../../pages/jinzhunzizhu/jianyiguanaipinkunsheng')); 
const Jingzhundetail = asyncComponent(() => import('../../../pages/jinzhunzizhu/pinkunshengchaxun/detail')); 
const Jingzhundetail2 = asyncComponent(() => import('../../../pages/jinzhunzizhu/yichangpinkunsheng/detail')); 
const Jingzhundetail3 = asyncComponent(() => import('../../../pages/jinzhunzizhu/jianyiguanaipinkunsheng/detail')); 
// 心理健康
// 心理健康分析
const Xinlijiankangfenxi = asyncComponent(() => import('../../../pages/XinLiJianKang/FenXi/index'));
// 心理健康预警
const Xinlijiankangyujing = asyncComponent(() => import('../../../pages/XinLiJianKang/YuJing/index'));
// 心理健康详情
const PersonDetail = asyncComponent(() => import('../../../pages/XinLiJianKang/YuJing/detail/index'));
// 心理访谈管理
const MentalhealthManagement = asyncComponent(() => import('app/MentalHealthcomponents/MentalhealthManagement'));
const MentalhealthDealComponent = asyncComponent(() => import('app/MentalHealthcomponents/MentalhealthDealComponent'));


//系统设置
//预警设置
const EarlyWarningSetting = asyncComponent(() => import('../../../pages/SystemSetup/EarlyWarningSetting')); 
const EarlyWarningConfigAdd = asyncComponent(() => import('../../../pages/SystemSetup/EarlyWarningSetting/EarlyWarningConfig/AddPage')); 
//预警白名单设置
const WhiteListConfig = asyncComponent(() => import('../../../pages/SystemSetup/EarlyWarningSetting/WhiteListConfig')); 
//预警白名单添加编辑页面
const WhitelistPersonalAdd = asyncComponent(() => import('../../../pages/SystemSetup/EarlyWarningSetting/WhiteListConfig/Personal/AddOrEditPage')); 
//预警白名单节假日添加编辑页面
const WhitelistHolidayAdd = asyncComponent(() => import('../../../pages/SystemSetup/EarlyWarningSetting/WhiteListConfig/Holiday/AddOrEditPage')); 
const DabiaoList =asyncComponent(() => import('../../../pages/SystemSetup/DabiaoJine')); 
const DabiaoDetail =asyncComponent(() => import('../../../pages/SystemSetup/DabiaoJine/detail'));
//预警处理达标配置页面
const ChuLiList =asyncComponent(() => import('../../../pages/SystemSetup/ChuLiDaoBiaoPeiZhi'));


// 权限管理
//账户管理
const Account = asyncComponent(() => import('../../../pages/AccessManage/Account')); 
//菜单管理
const Menu = asyncComponent(() => import('../../../pages/AccessManage/Menu')); 
//权限配置
const Authority = asyncComponent(() => import('../../../pages/AccessManage/Authority')); 



class RightComponent extends React.Component{
    render () {
        return (
            <div className='rightcomponent'>
                <div className='scrollcontainer'>
                    <Switch>
                        <Route path="/mainframe/xingweihuaxiang/all" component={All}/>
                        <Route path="/mainframe/xingweihuaxiang/personal" exact component={Personal}/>
                        <Route path="/mainframe/xingweihuaxiang/personal/details" component={PersonalDetails}/>

                        <Route path="/mainframe/comprehensivewarning" exact component={ComprehensiveWarning}/>
                        <Route path="/mainframe/comprehensivewarning/detail/:id" exact component={ComprehensiveWarningDetail}/>
                        <Route path="/mainframe/comprehensivewarning/overview" exact component={ComprehensiveWarningOverview}/>
                        
                        <Route path="/mainframe/jinzhunzizhu/pinkunshengfenxi" component={Pinkunshengfenxi}/>
                        <Route path="/mainframe/jinzhunzizhu/pinkunshengchaxun" component={Pinkunshengchaxun}/>
                        <Route path="/mainframe/jinzhunzizhu/yichangpinkunsheng" component={Yichangpinkunsheng}/>
                        <Route path="/mainframe/jinzhunzizhu/jianyiguanaipinkunsheng" component={Jianyiguanaipinkunsheng}/>
                        <Route path="/mainframe/jinzhunzizhu/detail" component={Jingzhundetail}/> 
                        <Route path="/mainframe/jinzhunzizhu/detail2" component={Jingzhundetail2}/>
                        <Route path="/mainframe/jinzhunzizhu/detail3" component={Jingzhundetail3}/>
                        <Route exact path="/mainframe/mentalhealth/analysis" component={Xinlijiankangfenxi}/>
                        <Route exact path="/mainframe/mentalhealth/warn" component={Xinlijiankangyujing}/>
                        <Route exact path="/mainframe/mentalhealth/warn/:id/:code/:name/:date" component={PersonDetail}/>
                        <Route exact path="/mainframe/mentalhealth/management" component={MentalhealthManagement}/>
                        <Route exact path="/mainframe/mentalhealth/management/:name/:code" component={MentalhealthDealComponent}/>
                        {/*心理健康预警的详情页面*/}
                        <Route path="/mainframe/XinLiJianKang/YuJing/detail/:id/:code" exact  component={PersonDetail}/>

                        <Route path="/mainframe/systemsetup/earlywarningsetting" component={EarlyWarningSetting}/>
                        <Route path="/mainframe/systemsetup/earlywarningconfigadd" component={EarlyWarningConfigAdd}/>
                        <Route exact path="/mainframe/systemsetup/earlywarningwhitelist" component={WhiteListConfig}/>
                        <Route path="/mainframe/systemsetup/earlywarningwhitelist/personal/addoredit" component={WhitelistPersonalAdd}/>
                        <Route path="/mainframe/systemsetup/earlywarningwhitelist/holiday/addoredit" component={WhitelistHolidayAdd}/>
                        <Route path="/mainframe/systemsetup/dabiao" component={DabiaoList}/>
                        <Route path="/mainframe/systemsetup/dabiaoDetail" component={DabiaoDetail}/>
                        <Route path="/mainframe/systemsetup/chuli" component={ChuLiList}/>
                        <Route path="/mainframe/accessmanage/account" component={Account}/>
                        <Route path="/mainframe/accessmanage/menu" component={Menu}/>
                        <Route path="/mainframe/accessmanage/authority" component={Authority}/>
                        
                        <Route path="/" render={(props) => <Redirect to='/mainframe/xingweihuaxiang/all'/>}/>
                    </Switch>
                </div>
            </div>
        )
    }
}

export default withRouter(RightComponent)


