import React from 'react';
import { withRouter} from "react-router-dom";
import { connect} from 'react-redux';
import { breadcrumbAction } from './../../../redux/actions/breadcrumbAction';
import { Menu } from 'antd';
import './leftcomponent.css'
import nav from './nav';
import { getLocal } from '../../../utils';

const SubMenu = Menu.SubMenu;

class LeftComponent extends React.Component{
	
    state = {
        current:'',
        openKeys:[],
        menus:[],
        rootSubmenuKeys: []
    }
    
    componentWillMount(){
        let userInfo = JSON.parse(getLocal('userInfo'));
        let menus = userInfo?userInfo.menus:[];
        let current = this.props.location.pathname;
        let openKeys = [];
        let rootSubmenuKeys = [];
        nav.forEach(item=>{
            if(item.subMenus){
                item.subMenus.forEach(ele=>{
                    if(ele.url === current){
                        openKeys = [item.name];
                    }
                })
            }
    		rootSubmenuKeys.push(item.name)
        })        
        let resMenu =[];
        menus && menus.forEach(element => {           
            if(nav.some(a=>a.id===element.id)){
                let item;
                const navItem = nav.filter(b=>b.id===element.id)[0];
                if(element.subMenus && navItem.subMenus && element.subMenus.length>0){
                    item = Object.assign({},element,{icon:navItem.icon});
                    navItem.subMenus.forEach((n)=>{
                        item.subMenus.forEach((c,i)=>{
                            if(c.id===n.id){                                
                                item.subMenus[i] = Object.assign({},c,{url:n.url});
                            }
                        })
                    })
                }  else {
                    item = Object.assign({},element,{icon:navItem.icon,url:navItem.url});
                }
                resMenu.push(item);
            }
        });
        this.setState({
            current,
            openKeys,
            rootSubmenuKeys,
            menus:resMenu
        })
        
    }
    
    selectClick = ({ key })=> {
        this.setState({
            current: key
        });
        this.props.history.push(key);
        this.props.changeBreadcrumb(key);
    }
    
    onOpenChange = (openKeys) => {
		const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
		if(this.state.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
			this.setState({
				openKeys
			});
		} else {
			this.setState({
				openKeys: latestOpenKey ? [latestOpenKey] : [],
			});
		}
	}
    
    render () {
        return (
            <div className='leftcomponent'>
                <div className="scrollconstainer">
                    <Menu
                        onClick={this.selectClick}
                        onOpenChange={this.onOpenChange}
                        selectedKeys={this.state.current ? [this.state.current] : null}
                        openKeys={this.state.openKeys}
                        mode="inline"
                        theme="dark">
                        {this.state.menus.map(item=>{
                            if(item.subMenus && item.subMenus.length>0){
                                return (
                                <SubMenu key={item.name} title={<span>{item.icon}<span>{item.name}</span></span>}>
                                    {item.subMenus.map(child=>(<Menu.Item key={child.url}>
                                            {child.name}
                                        </Menu.Item>))}
                                </SubMenu>)
                            } else {
                                return (<Menu.Item key={item.url}>
                                    <span>{item.icon}</span>{item.name}
                                </Menu.Item>)
                            }
                        })}
                    </Menu>
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
    	changeBreadcrumb(curUrl){
    		const action  = breadcrumbAction(curUrl);
			dispatch(action);
    	}
	}
}

export default connect(null, mapDispatchToProps)(withRouter(LeftComponent))


